import { BrowserModule } from '@angular/platform-browser';
import { CommonModule } from '@angular/common';
import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { RouterModule } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';

import { AppComponent } from './app.component';
import { ServiceModule } from './services/service.module';
import { ComponentModule } from './shareds/components/component.module';
import { PageNotFoundComponent } from './shareds/components/pageNotFound/pageNotFound.component';


@NgModule({
  declarations: [AppComponent],
  imports: [ 
    CommonModule,
    BrowserModule,
    ComponentModule,
    HttpClientModule,
    ServiceModule.forRoot(),
    RouterModule.forRoot([
      { path: '', redirectTo: '/glaubiger', pathMatch: 'full' },
      { path: 'glaubiger', loadChildren: './pages/glaubiger/glaubiger.module#GlaubigerModule' },
      { path: '**', component: PageNotFoundComponent }
    ], { useHash: false })
  ],
  bootstrap: [AppComponent],
  schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class AppModule { }



