import { Injectable } from "@angular/core";
import { Observable } from "rxjs/Observable";
import { HttpClient } from '@angular/common/http';

@Injectable()
export class GlaubigerService {

    private baseUrl: string = "/api/glaubigers";

    constructor(private http: HttpClient) {

    }

    getList(isFilter): Observable<any> {
        let apiUrl = this.baseUrl;
        if (!isFilter) apiUrl = `${apiUrl}?glaubiger=true`;
        return this.http.get(apiUrl);
    }
    getPersons(personId?) {
        let apiUrl = '/api/persons';
        if (personId) apiUrl = `${apiUrl}?baPersonId=${personId}`;
        return this.http.get(apiUrl);
    }
    getTreeMenus() {
        let apiUrl = '/api/treemenus';
        return this.http.get(apiUrl);
    }

    updateGlaubiger(model) {
        let apiUrl = `${this.baseUrl}/${model.id}`;
        return this.http.put(apiUrl, model);
    }
}


