import { NgModule, ModuleWithProviders } from '@angular/core';
import { HttpClientInMemoryWebApiModule } from 'angular-in-memory-web-api';

import { MockDataService } from './service.mockdata';
import { GlaubigerService } from './service.glaubiger';

const PROVIDERS = [
    { provide: GlaubigerService, useClass: GlaubigerService }
];
@NgModule({
    imports: [
        HttpClientInMemoryWebApiModule.forRoot(MockDataService, { dataEncapsulation: false })
    ]
})
export class ServiceModule {
    static forRoot(): ModuleWithProviders {
        return <ModuleWithProviders>{
            ngModule: ServiceModule,
            providers: [...PROVIDERS]
        };
    }
}



