export class PersonModel {
    baPersonId: any;
    personGeburtsdatum: any;
    datum: any;
    name: any;
    kontorNr: any;
    constructor(baPersonId, personGeburtsdatum, datum, name, kontorNr) {
        this.baPersonId = baPersonId;
        this.personGeburtsdatum = personGeburtsdatum;
        this.datum = datum;
        this.name = name;
        this.kontorNr = kontorNr;
    }
}


